// FileDbDataService.js
import http from "../http-common";

class FileDbDataService {
    // Ìï®ÏàòÎ™Ö : upload() Ìï®Ïàò ( DB Ï†ÄÏû• Ìï®Ïàò )
    // Îß§Í∞úÎ≥ÄÏàò title : Ï†úÎ™©
    //          content : ÎÇ¥Ïö©
    //          fileDb : ÏóÖÎ°úÎìú ÎåÄÏÉÅ Ïù¥ÎØ∏ÏßÄÍ∞Ä Ï†ÄÏû•ÎêòÎäî Í∞ùÏ≤¥ Î∞∞Ïó¥
    upload(title, content, fileDb) {    // üò¶
        //                 Í∞ùÏ≤¥***
        // json Í∞ùÏ≤¥ ÏÇ¨Ïö©ÌïòÏßÄ ÏïäÏùå (x)
        // form ÌÉúÍ∑∏Î°ú Ï†ÑÏÜ°ÌïòÎäî Î∞©ÏãùÏùÑ Ïù¥Ïö©Ìï®
        // html <form></form> == js FormData Í∞ùÏ≤¥Î°ú ÏÇ¨Ïö©Í∞ÄÎä•
        //                       .append("ÏÜçÏÑ±Î™Ö", Í∞í) Ìï®Ïàò : Îç∞Ïù¥ÌÑ∞Î•º Ï†ÄÏû•
        // axios Ìï®Ïàò : .post()
        // Ìó§Îçî : "Content-Type" : "multipart/form-data" 
        let formData = new FormData(); // Ìèº(form) Í∞ùÏ≤¥ ÏÉùÏÑ±

        formData.append("fileTitle", title);
        formData.append("fileContent", content);
        formData.append("fileDb", fileDb);

        return http.post("/fileDb/upload", formData, {
            headers: {
                "Content-Type" : "multipart/form-data"
            }
        })
    }

    // Ïù¥ÎØ∏ÏßÄ Ï†úÎ™© Í≤ÄÏÉâ ÏöîÏ≤≠ Ìï®Ïàò
    getFiles(fileTitle, page, size) {
        console.log(fileTitle);
        console.log(page);
        console.log(size);
        return http.get(`/fileDb?fileTitle=${fileTitle}&page=${page}&size=${size}`)
    }

    // Ïù¥ÎØ∏ÏßÄ ÏÇ≠Ï†ú ÏöîÏ≤≠ Ìï®Ïàò
    delete(fid) {
        return http.delete(`/fileDb/deletion/${fid}`);
    }

}

export default new FileDbDataService();